body {
  display: flex;
  justify-content: center;
  font-family: "Georama", sans-serif;
  padding: 1rem;
}
.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
  padding: 1rem;
  width: 40%;
  height: auto;
  box-shadow: 0px 0px 2px;
  background: rgb(244, 232, 255);
}
.quiz-label {
  font-size: 1rem;
}
nav {
  background-color: #5454d4;
  height: 9vh;
  display: flex;
  align-items: center;
  border-radius: 3px;
}
nav a {
  color: white;
  text-decoration: none;
  padding: 12px;
  font-size: 1.2rem;
}
h2 {
  display: flex;
  justify-content: center;
  font-size: 2rem;
  margin-top: 2rem;
}
h1 {
  display: flex;
  justify-content: center;
  font-size: 2rem;
  margin-top: 2rem;
}
label {
  font-size: 1.3rem;
  margin: 4px;
  margin-left: 2rem;
}
input {
  height: 30px;
  margin-bottom: 1.3rem;
  margin-left: 1.5rem;
}
.input1 {
  width: 90%;
  height: 30px;
  border: none;
  outline: none;
  border: 1px solid rebeccapurple;
  border-radius: 5px;
}
button {
  margin-top: 2rem;
  width: 45%;
  height: 2.5rem;
  margin-left: 10rem;
  background-color: #00008b;
  color: white;
  font-size: 1.1rem;
  border-radius: 5px;
  cursor: pointer;
}
button:hover {
  background-color: white;
  border: 2px solid #00008b;
  color: #00008b;
}
button:focus {
  color: bold white 900;
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
}

.output {
  margin-top: 2rem;
  font-size: 1.5rem;

  display: flex;
  justify-content: center;
}

/* responsiveness */
@media (max-width: 800px) {
  body {
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .container {
    width: 97%;
    padding: 0rem;
    margin: 2rem;
  }
  label {
    margin-left: 1.4rem;
  }
  input {
    margin-left: 1rem;
  }
  .btn {
    display: flex;
    justify-content: center;
    margin-left: 6.5rem;
  }
}
